/* Sidebar tổng thể */
.sidebar {
   margin-top: 2px;
    width: 220px;
    height: 93%;
    background: linear-gradient(135deg, #2c3e50, #34495e);
    color: white;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 4px 0 10px rgba(0, 0, 0, 0.3);
}

/* Tiêu đề Sidebar */
.sidebar-title {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
    text-transform: uppercase;
    letter-spacing: 1px;
}

/* Menu */
.sidebar-menu {
    width: 100%;
}

/* Nút Sidebar */
.sidebar-button {
    display: block;
    width: 100%;
    padding: 12px 0;
    text-align: center;
    color: white;
    font-size: 16px;
    font-weight: 500;
    text-decoration: none;
    background: #1f2c3d;
    margin: 8px 0;
    border-radius: 6px;
    transition: all 0.3s ease;
}

/* Hiệu ứng hover */
.sidebar-button:hover {
    background: #3498db;
    transform: scale(1.05);
}
